import Vue from 'vue'
import Router from 'vue-router'
import HomeIndex from '../views/Main'
import {sdk, browser} from '@/common/util/bitUtil'

Vue.use(Router)

const NotFound = () => import('../views/NotFound.vue')

let vueRouter = new Router({
    // mode: 'history',
    routes: [
        {
            path: '/',
            name: 'home',
            component: HomeIndex,
            meta: {
                title: '游戏中心',
                keepAlive: true
            },
        },
        {
            path: '/tower',
            component: () => import('../views/tower/Index'),
            name: 'tower',
            meta: {
                title: '盖楼游戏'
            }
        },
        {
            path: '/2048',
            component: () => import('../views/2048/Index'),
            name: '2048',
            meta: {
                title: '2048'
            }
        },
        {
            path: '/jump',
            component: () => import('../views/jump/Index'),
            name: 'jump',
            meta: {
                title: 'jump'
            }
        },
        {
            path: '/jumpone',
            component: () => import('../views/jumpone/Index'),
            name: 'jumpone',
            meta: {
                title: '跳一跳'
            }
        },
        {
            path: '/tetris',
            component: () => import('../views/tetris/Index'),
            name: 'tetris',
            meta: {
                title: '俄罗斯方块'
            }
        },
        {
            path: '/riddle',
            component: () => import('../views/riddle/Index'),
            name: 'riddle',
            meta: {
                title: 'riddle'
            }
        },
        {
            path: '/phaser2',
            component: () => import('../views/phaser2/Index'),
            name: 'phaser2',
            meta: {
                title: 'phaser2'
            }
        },
        {
            path: '/flappybird',
            component: () => import('../views/flappybird/Index'),
            name: 'flappybird',
            meta: {
                title: 'flappybird'
            }
        },
        {
            path: '/404',
            component: NotFound,
            name: '404',
        }, {
            path: '*',
            component: NotFound,
            name: '未知页面',
        }
    ]
})

vueRouter.beforeEach((to, from, next) => {
// 设置标题
    if (to.meta && to.meta.title) {
        document.title = to.meta.title
    }

    if (window._hmt && to.path) {
        _hmt.push(['_trackPageview', '/#' + to.fullPath]);
    }

    next()

});

export default vueRouter
