import {browser, sdk} from "./bitUtil";

export const ONE_SECOND = 1000
export const ONE_MINUTE = ONE_SECOND * 60
export const ONE_HOUR = ONE_MINUTE * 60
export const ONE_DAY = ONE_HOUR * 24

export const TIME = {
    ONE_SECOND,
    ONE_MINUTE,
    ONE_HOUR,
    ONE_DAY
}

export function dateFormat(date, fmt = 'YYYY-MM-DD HH:mm:ss') {
    if (!date) {
        return ''
    }
    if (typeof date === 'string') {
        date = new Date(date.replace(/-/g, '/'))
    }
    if (typeof date === 'number') {
        date = new Date(date)
    }
    var o = {
        'M+': date.getMonth() + 1,
        'D+': date.getDate(),
        'h+': date.getHours() % 12 === 0 ? 12 : date.getHours() % 12,
        'H+': date.getHours(),
        'm+': date.getMinutes(),
        's+': date.getSeconds(),
        'q+': Math.floor((date.getMonth() + 3) / 3),
        'S': date.getMilliseconds()
    }
    var week = {
        '0': '\u65e5',
        '1': '\u4e00',
        '2': '\u4e8c',
        '3': '\u4e09',
        '4': '\u56db',
        '5': '\u4e94',
        '6': '\u516d'
    }
    if (/(Y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    if (/(E+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, ((RegExp.$1.length > 1) ? (RegExp.$1.length > 2 ? '\u661f\u671f' : '\u5468') : '') + week[date.getDay() + ''])
    }
    for (var k in o) {
        if (new RegExp('(' + k + ')').test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
        }
    }
    return fmt
}

export function getQueryByName(name, query = '') {
    let value = ''
    if (name) {
        let reg = new RegExp('(?:^|\\?|\\&)' + name + '=([^&]*)(&|$)', 'i')
        value = (query.match(reg) || [])[1] || ''
    }
    return value
}


export function formatList(resultList, originList, isUp) {
    let list = resultList
    if (typeof isUp === "boolean" && !isUp) {
        list = [...originList, ...resultList]
    }
    return list
}

export function formatQuery(isUp, query, page = 0) {
    query.page = page
    if (typeof isUp === "boolean" && !isUp) {
        query.page = page + 1
    }
    return query
}

export function queryString(map = {}) {
    let array = []
    for(let i in map){
        array.push(i + '=' + encodeURIComponent(map[i]))
    }
    return array.join('&')
}

export function formatSortQuery(isUp, list = [], query = {}) {
    if (typeof isUp === 'boolean' && list.length > 0) {
        const first = list[0]
        const last = list[list.length - 1]
        query.sort = (isUp ? first.sort : last.sort) || ''
        query.id = (isUp ? first.id : last.id) || ''
        query.up = isUp
    }
    return query
}

export function formatResultList(isUp, originList = [], resultList = []) {
    let list = []
    if (typeof isUp === 'boolean') {
        if (resultList.length > 0) {
            if (isUp) {
                list = [...resultList, ...originList]
            } else {
                list = [...originList, ...resultList]
            }
        } else {
            list = originList
        }
    } else {
        list = resultList
    }
    return list
}

export function formatPrice(num = 0, precision) {
    if (typeof precision !== 'number') {
        precision = 2
        // if (num % 10 === 0) {
        //     precision = 1
        // }
    }
    return (num / 100).toFixed(precision)
}

export const CANDY_PRECISION = 2

export function formatCandy(num) {
    return formatPrice(num, CANDY_PRECISION)
}

export function formatRealCandy(num) {
    return formatPrice(num, 8)
}

export function formatRate(num, rate) {
    return num / rate
}


export function formatQuantity(num = 0, precision = 2) {
    if (num < 10000) {
        return num
    } else {
        return (num / 10000).toFixed(precision) + '万'
    }
}

const LOCAL_RATE = 'local_rate'

export function getLocalRate() {
    const value = localStorage[LOCAL_RATE] || 20
    return parseFloat(value)
}

export function setLocalRate(value = '') {
    localStorage[LOCAL_RATE] = value
}

export function getUid() {
    if(browser.app){
        return sdk.getUid() || ''
    } else {
        return '123456789'
    }
}

