function formatCallback(cb) {
    return function (text) {
        let data = {}
        if(typeof text === "string"){
            try{
                data = JSON.parse(text)
            }catch (e) {
                console.log(e)
            }
        } else {
            data = text
        }
        cb(data)
    }
}

