        methods: {
            redirect(){

            },
            redirectAction(item){
                let query = ''
                if(item.query){
                    query = '?' + queryString(item.query)
                }
                let url = location.origin + '#/' + item.name  + query
                if(browser.app){
                    location.href =  'tielement://bigfishnet.vip/app/web?url=' + encodeURIComponent(url)
                } else {
                    window.open(url, '_blank')
                }
            }
        },
        mounted() {
        }
    }
