import Vue from 'vue'
import Vuex from 'vuex'
import {getUid} from "../common/util";

Vue.use(Vuex)

const DEFAULT_USER = {
    avatar: '',
    nickName: '登录/注册',
    accountId: ''
}

export default new Vuex.Store({
    state: {
        isWxAuth: false,
        isLogin: false,
        userId: '',
        userInfo: Object.assign({}, DEFAULT_USER),
        countReturn: 0,
        scrollTop: 0
    },
    mutations: {
        setWxAuth(state, bool) {
            state.isWxAuth = !!bool
        },
        getUserId(state){
            const uuid = getUid()
            console.log("uuid", uuid)
            state.userId = uuid
            state.isLogin = !!uuid
        },
        setTop(state, payload = 0){
            state.scrollTop = payload
        }
    },
    actions: {

    }
})
